const existingAddrRadio = document.querySelector('label[for="existing-addr"]');
const existingAddrBox = document.querySelector('.existing-addr-box');
const newAddrRadio = document.querySelector('label[for="new-addr"]');
const newAddrBox = document.querySelector('.new-addr-box');

if (existingAddrRadio !== null) {
    existingAddrRadio.addEventListener('click', function() {
        existingAddrBox.classList.add('active');
        newAddrBox.classList.remove('active');
    });

    newAddrRadio.addEventListener('click', function() {
        existingAddrBox.classList.remove('active');
        newAddrBox.classList.add('active');
    });
}
